'use client';
import { useState } from 'react';

export default function Home() {
  const [isDemoOpen, setIsDemoOpen] = useState(false);
  const [isQuoteOpen, setIsQuoteOpen] = useState(false);
  const [callStatus, setCallStatus] = useState('idle'); // idle, calling, connected, analyzing, done

  const handleStartDemo = () => {
    setIsDemoOpen(true);
    setCallStatus('calling');
    // Simulation of Retell Call
    setTimeout(() => setCallStatus('connected'), 2000);
  };

  const handleEndDemo = () => {
    setCallStatus('analyzing');
    // Simulate backend processing time
    setTimeout(() => {
      setCallStatus('done');
    }, 3000);
  };

  return (
    <main>
      {/* Hero Section */}
      <section className="hero">
        <div className="container">
          <h1>Voice-First Scheduling for Modern Advisors</h1>
          <p>
            CallNest converts your client conversations into structured appointments,
            calendar actions, and follow-up checklists automatically using AI.
          </p>
          <div className="hero-actions">
            <button className="btn btn-primary" onClick={handleStartDemo}>Try Demo Call</button>
            <button className="btn btn-secondary" onClick={() => setIsQuoteOpen(true)}>Request a Quote</button>
          </div>
        </div>
      </section>

      {/* Intents Section */}
      <section className="features">
        <div className="container">
          <h2 className="section-title">What CallNest Can Handle</h2>
          <div className="grid">
            <div className="card">
              <h3>Book Appointments</h3>
              <p>Intelligent slot negotiation and calendar booking directly from voice.</p>
            </div>
            <div className="card">
              <h3>Reschedule</h3>
              <p>Find new times and move appointments effortlessly.</p>
            </div>
            <div className="card">
              <h3>Cancellations</h3>
              <p>Process cancellations and update records instantly.</p>
            </div>
            <div className="card">
              <h3>Pre-Consultation</h3>
              <p>Advise clients on documents and prep needed for their meeting.</p>
            </div>
            <div className="card">
              <h3>Availability Checks</h3>
              <p>Share open windows without locking slots unnecessarily.</p>
            </div>
          </div>
        </div>
      </section>

      {/* Demo Modal */}
      {isDemoOpen && (
        <div className="modal-overlay">
          <div className="modal-content">
            <button className="close-btn" onClick={() => setIsDemoOpen(false)}>&times;</button>
            <h2 style={{ marginBottom: '1rem', textAlign: 'center' }}>Live Demo</h2>

            {callStatus === 'calling' && (
              <div className="call-status">
                <div className="pulsing-circle"></div>
                <p>Connecting to AI Agent...</p>
              </div>
            )}

            {callStatus === 'connected' && (
              <div className="call-status">
                <div className="pulsing-circle" style={{ background: '#22c55e' }}></div>
                <p>Call in Progress</p>
                <p style={{ fontSize: '0.9rem', color: '#94a3b8', marginTop: '1rem' }}>
                  Speak naturally. Try booking an appointment.
                </p>
                <button className="btn btn-secondary" style={{ marginTop: '2rem' }} onClick={handleEndDemo}>
                  End Call
                </button>
              </div>
            )}

            {callStatus === 'analyzing' && (
              <div className="call-status">
                <p>Analyzing conversation...</p>
                <p className="text-muted">Extracting intents and entities...</p>
              </div>
            )}

            {callStatus === 'done' && (
              <div className="call-status">
                <h3 style={{ color: '#22c55e' }}>Analysis Complete</h3>
                <p>The backend has received the summary and routed the intent.</p>
                <button className="btn btn-primary" style={{ marginTop: '1rem' }} onClick={() => setIsDemoOpen(false)}>Close</button>
              </div>
            )}
          </div>
        </div>
      )}

      {/* Quote Modal */}
      {isQuoteOpen && (
        <div className="modal-overlay">
          <div className="modal-content">
            <button className="close-btn" onClick={() => setIsQuoteOpen(false)}>&times;</button>
            <h2>Request a Quote</h2>
            <form style={{ marginTop: '1rem' }} onSubmit={(e) => { e.preventDefault(); alert('Quote Request Sent!'); setIsQuoteOpen(false); }}>
              <div className="form-group">
                <label>Name</label>
                <input type="text" className="form-input" required />
              </div>
              <div className="form-group">
                <label>Email</label>
                <input type="email" className="form-input" required />
              </div>
              <div className="form-group">
                <label>Message</label>
                <textarea className="form-input" rows={3}></textarea>
              </div>
              <button type="submit" className="btn btn-primary" style={{ width: '100%' }}>Send Request</button>
            </form>
          </div>
        </div>
      )}
    </main>
  );
}
